import { withLDConsumer } from "launchdarkly-react-client-sdk";
import React from "react";

const InputBox = ({ flags, ldClient /*, ...otherProps */ }) => {
  class MyInputBox extends React.Component {
    constructor(props) {
      super(props);
      this.handleSubmit = this.handleSubmit.bind(this);
      this.input = React.createRef();
    };

    handleSubmit(event) {
      event.preventDefault();
      //const ldClient = useLDClient();
      let context = ldClient.getContext();
      context.key = this.input.current.value;
      ldClient.identify(context);
      //.then(
      //alert('You entered: ' + this.input.current.value + ' -- ' + JSON.stringify(context))
      //);
    };

    render() {
      let showFeature = ldClient.variation("experimental-ui-change");

      return showFeature === "variant A" ? (
        <form onSubmit={this.handleSubmit}>
          <label>
            Who are you?
            <input type="text" ref={this.input} minlength="2" maxlength="8" size="8" />
          </label>
          <input type="submit" value="Submit" />
        </form>
      ) : (
        <div/>
      );
    };
  };

  return ( <MyInputBox /> );
};

export default withLDConsumer()(InputBox);